
import { defineComponent } from 'vue'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';
import { ICM21300111_RES, ICM21300111_REQ, ATMInfoList } from '@/shared/type/ICM21300111.type';
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: 'icm213001sc00',
    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const atmInfoList = [] as ATMInfoList[];
        const searchForm = {
            searchTypeCode: '',
            displayYN: '',
            searchValue: '',
            statusCode: ''
        }
        const header = [
            { columnName: "ATM Code" },
            { columnName: "ATM Name" },
            { columnName: "Branch Name" },
            { columnName: 'Status' },
            { columnName: "Address" },
            { columnName: "Contact" },
            { columnName: "Created date" },
        ];
        const searchCode = [
            { key: "001", value: "ATM Code" },
            { key: "002", value: "ATM Name" },
            { key: "003", value: "Contact" },
        ];
        return {
            atmInfoList,
            searchForm,
            header,
            searchCode,
            COMMON_KEY_VALUE,
            totalItems: 0,
            currentPage: 1,
            pageSize: 10,
            startingIndex: 1
        }
    },

    mounted() {
        this.requestATMInfoList();
    },

    methods: {
        async requestATMInfoList() {
            const reqBody = {
                pageSize: this.pageSize,
                displayYN: this.searchForm.displayYN,
                searchTypeCode: this.searchForm.searchTypeCode,
                searchValue: this.searchForm.searchValue,
                pageNumber: this.currentPage,
                statusCode: this.searchForm.statusCode
            } as ICM21300111_REQ;
            const res = await this.$request.getRequest('ICM21300111', reqBody, false) as ICM21300111_RES;
            this.atmInfoList = res?.body.list.map((item: ATMInfoList) => {

                return {
                    atmName: item.atmName,
                    firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                    displayYN: this.$codeConverter.convertValue('displayYN', item.displayYN),
                    atmCode: item.atmCode,
                    atmAddress: item.atmAddress,
                    atmTel: this.$format.formatPhoneNumber(item.atmTel),
                    branchCode: item.branchCode,
                    branchName: item.branchName,
                    statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("ContentManangementStatusCode", item.statusCode)}`,
                }
            })
            this.totalItems = res.body.totalCount;
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `ICM213001SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.requestATMInfoList();
                    } else {
                        this.requestATMInfoList();
                    }
                    break;
                default:
                    this.searchForm.searchValue = '';
                    this.searchForm.searchTypeCode = '';
                    this.searchForm.displayYN = '';
                    this.searchForm.statusCode = ''
                    this.requestATMInfoList();
                    break;
            }
        },

        handleRowClick(item) {
            this.$router.push({
                path: `ICM213001SC01`,
                query: {
                    type: "edit",
                    atmCode: item.atmCode,
                    branchCode: item.branchCode
                }
            });
        },

        handlePagination(pageNumber: number) {
            this.currentPage = pageNumber
            this.requestATMInfoList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        }
    }
})
